.event-body-container {
    padding: 120px 180px;
    text-align: center;
}

.event-body-container .page-title {
    display: inline-block;
    border-top: 5px double #DED90D;
    border-bottom: 5px double #DED90D;
    margin-bottom: 100px;
    margin-top: 80px;
    padding: 5px;

    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px;
    letter-spacing: 0.24em;

    color: #FFFFFF;
}

.event-hr {
    border-bottom: 1px solid #fff;
    margin: 80px 0 40px 0;
}

@media screen and (max-width: 1200px) {
    .event-body-container {
        padding: 100px 60px 0 60px;
    }
}

@media screen and (max-width: 960px) {
    .event-body-container {
        padding: 50px 60px;
    }
    .event-body-container .page-title {
        margin-bottom: 30px;

    }
}